@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,wght@0,700;1,700&display=swap);
.Dashboard {
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  padding-left: 10%;
  padding-right: 10%;
  padding-top: 80px;
  padding-bottom: 30px;
  text-align: center; }

.InfoMain {
  color: #325784;
  font-size: 14px;
  padding-top: 18px;
  padding-left: 15px;
  text-align: center; }

.BothChartsOn {
  display: inline; }

.BothChartsOff {
  display: none; }

.DifficultChartOn {
  display: inline; }

.DifficultChartOff {
  display: none; }

.FunChartOn {
  display: inline; }

.FunChartOff {
  display: none; }

.topbuttons {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  -webkit-transition: all 0.4s;
  transition: all 0.4s; }

.topbuttons button {
  width: 150px;
  height: 30px;
  border-radius: 15px;
  font-size: 16px;
  font-family: "Nunito Sans", sans-serif;
  margin: 5px;
  border: none;
  color: white;
  -webkit-transition: all 0.3s;
  transition: all 0.3s; }

.topbuttons button:focus {
  outline: none;
  border: 2px solid #4b90e2; }

.difficultyButton {
  background-color: #efca5b; }

.difficultyButton:hover {
  background-color: #ecba1e; }

.funButton {
  background-color: #6baeff; }

.funButton:hover {
  background-color: #4b90e2; }

.bothButton {
  background-image: -webkit-gradient(linear, left top, right top, from(#efca5b), to(#6baeff));
  background-image: linear-gradient(to right, #efca5b, #6baeff); }

.bothButton:hover {
  background-image: -webkit-gradient(linear, left top, right top, from(#ecba1e), to(#4b90e2));
  background-image: linear-gradient(to right, #ecba1e, #4b90e2); }

@media screen and (max-width: 879px) {
  .Dashboard {
    padding-top: 115px; }
  .InfoMain {
    font-size: 10px; } }

@media screen and (max-width: 480px) {
  .Dashboard {
    padding-top: 150px; } }

.Studentlist {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-top: 10px; }

.Header {
  text-align: center;
  background-color: #4b90e2;
  text-transform: uppercase;
  font-weight: 900;
  padding-top: 5px;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.75);
  position: fixed;
  width: 100%;
  z-index: 2;
  font-size: 20px; }

.Footer {
  text-align: center;
  background-color: #4b90e2;
  position: fixed;
  width: 100%;
  z-index: 2;
  bottom: 0px;
  font-size: 12px;
  box-shadow: 0px -2px 5px 0px rgba(0, 0, 0, 0.75); }

button {
  width: 70px;
  font-family: "Nunito Sans", sans-serif;
  margin: 5px;
  border: none;
  height: 25px;
  border-radius: 13px;
  color: white;
  background-color: #4b90e2;
  padding: 0px;
  -webkit-transition: all 0.4s;
  transition: all 0.4s; }

button:hover {
  background-color: #95c5ff;
  cursor: pointer; }

button:focus {
  outline: none;
  border: 1px solid white; }

.headerimage {
  position: fixed;
  right: 3px;
  top: 3px;
  width: 30px;
  text-align: center; }

.Container {
  font-family: "Nunito Sans", sans-serif;
  color: white; }

.Profilepic {
  width: 90px;
  height: 90px;
  float: left;
  position: relative;
  border-radius: 70px;
  margin: 20px;
  border: 3px solid #4b90e2; }

.Info {
  color: #325784;
  font-size: 14px;
  padding-top: 20px;
  text-align: left;
  width: 250px; }

.StudentInfo {
  display: flex;
  justify-content: center; }

@media screen and (max-width: 879px) {
  .Info {
    font-size: 10px;
    width: 200px; }
  .Profilepic {
    width: 70px;
    height: 70px; } }

body {
  margin: 0;
  font-family: 'Nunito Sans', sans-serif;
  font-family: "Nunito Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white; }

